.side-menu-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 999;
}

.side-menu {
  position: fixed;
  top: 0;
  left: -300px;
  width: 300px;
  height: 100%;
  background-color: white;
  box-shadow: 2px 0 5px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease-in-out;
  z-index: 1000;
  overflow-y: auto;
}

.side-menu.open {
  transform: translateX(300px);
}

.side-menu-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  border-bottom: 1px solid #eee;
}

.side-menu-header h3 {
  margin: 0;
  color: #333;
}

.close-button {
  background: none;
  border: none;
  font-size: 24px;
  cursor: pointer;
  color: #666;
}

.districts-list.expanded {
  padding: 20px;
}

.district-item {
  padding: 5px;
  /* margin-bottom: 8px; */
  cursor: pointer;
  border-radius: 4px;
  transition: background-color 0.2s;
}

.district-item:hover {
  background-color: #f5f5f5;
}

/* Add hamburger menu button styles */
.menu-button {
  display: none;
  /* position: fixed; */
  /* top: 20px; */
  /* left: 20px; */
  height: auto;
  z-index: 999;
  background: white;
  border: none;
  padding: 10px;
  border-radius: 4px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  cursor: pointer;
}

/* Media queries for responsive design */
@media screen and (max-width: 768px) {
  .menu-button {
    display: block;
  }
  
  .map {
    margin-top: 60px; /* Add space for the menu button */
  }
}

.provinces-list {
  padding: 10px;
}

.province-section {
  margin-bottom: 10px;
}

.province-header {
  display: flex;
  justify-content: space-between;
  padding: 8px 12px;
  background-color: #f0f0f0;
  cursor: pointer;
  border-radius: 4px;
  font-weight: bold;
}

.province-header:hover {
  background-color: #e0e0e0;
}

.expand-icon {
  font-weight: bold;
}

.districts-list {
  margin-left: 15px;
  /* margin-top: 5px; */
  overflow: hidden;
  max-height: 0;
  transition: max-height 0.3s ease-out;
}

.province-section .districts-list.expanded {
  max-height: 500px; /* Large enough to contain all districts */
  transition: max-height 0.5s ease-in;
}

.district-item {
  transform: translateX(-20px);
  opacity: 0;
  transition: transform 0.3s ease, opacity 0.3s ease;
}

.districts-list.expanded .district-item {
  transform: translateX(0);
  opacity: 1;
}

.districts-list.expanded .district-item:nth-child(1) { transition-delay: 0.1s; }
.districts-list.expanded .district-item:nth-child(2) { transition-delay: 0.15s; }
.districts-list.expanded .district-item:nth-child(3) { transition-delay: 0.2s; }
.districts-list.expanded .district-item:nth-child(4) { transition-delay: 0.25s; }
.districts-list.expanded .district-item:nth-child(5) { transition-delay: 0.3s; }